const actions = {
  getReturnReasons: (page, search, filter) => ({
    type: 'GET_RETURN_REASONS_REQUEST',
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  createReturnReason: () => ({
    type: 'CREATE_RETURN_REASON_REQUEST'
  }),
  storeReturnReason: data => ({
    type: 'STORE_RETURN_REASON_REQUEST',
    payload: { body: data }
  }),
  editReturnReason: id_return_reason => ({
    type: 'EDIT_RETURN_REASON_REQUEST',
    payload: { id_return_reason: id_return_reason }
  }),
  updateReturnReason: (id_return_reason, data) => ({
    type: 'UPDATE_RETURN_REASON_REQUEST',
    payload: {
      id_return_reason: id_return_reason,
      body: data
    }
  }),
  showReturnReason: id_return_reason => ({
    type: 'SHOW_RETURN_REASON_REQUEST',
    payload: { id_return_reason: id_return_reason }
  }),
  deactivateReturnReason: id_return_reason => ({
    type: 'DEACTIVATE_RETURN_REASON_REQUEST',
    payload: { id_return_reason }
  }),
  activateReturnReason: id_return_reason => ({
    type: 'ACTIVATE_RETURN_REASON_REQUEST',
    payload: { id_return_reason }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};

export default actions;
